/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.qodef-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: top;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }
    
    &:hover {
        
        .qodef-sc-dropdown {
            max-height: 100vh;
            pointer-events: auto;
            transition: .5s cubic-bezier(0.66, 0.14, 0.58, 1) .1s;
        }
    }

    .qodef-shopping-cart-inner {
        @include qodefRelativeHolderLayout();
        height: 100%;
    }

    .qodef-header-cart {
        position: relative;
        display: flex;
        align-items: center;
        height: 100%;

        .qodef-sc-opener-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            line-height: 1;
        }

        .qodef-sc-opener-count {
            font-size: 10px;
            color: #fff;
            background-color: #000;
            border-radius: 50%;
            position: absolute;
            top: 0;
            left: 100%;
            text-align: center;
            line-height: 13px !important;
            width: 15px;
            height: 15px;
            transform: translateX(-40%);
        }

        &.qodef-header-cart-icon-pack {
            
            .qodef-sc-opener-icon {
                font-size: 18px;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.qodef-header-cart-svg-path {
            
            .qodef-sc-opener-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }
}

.qodef-dark-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.qodef-light-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.qodef-sc-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    text-align: left;
    box-sizing: border-box;
    max-height: 0;
    pointer-events: none;
    overflow: hidden;
    will-change: transform;
    transition: .2s;
    
    .qodef-sc-dropdown-inner {
        @include qodefRelativeHolderLayout();
        width: 255px;
        padding: 15px;
        background-color: #fff;
        border: 1px solid $default-border-color;
        box-sizing: border-box;
    }
    
    .qodef-sc-dropdown-items {
        @include qodefRelativeHolderLayout();
    }
    
    .qodef-sc-dropdown-item {
        position: relative;
        display: flex;
        align-items: center;
        margin-top: 10px;
        
        &:first-child {
            margin-top: 0;
        }
    }
    
    .qodef-sc-dropdown-item-image {
        flex-shrink: 0;
        width: 71px;
        margin-right: 20px;
        
        a, img {
            display: block;
        }
    }
    
    .qodef-sc-dropdown-item-content {
        padding-right: 20px;
    }
    
    .qodef-sc-dropdown-item-title {
        margin: 0;
    }
    
    .qodef-sc-dropdown-item-price {
        margin: 3px 0 0;
    }
    
    .qodef-sc-dropdown-item-quantity {
        margin: 3px 0 0;
    }
    
    .qodef-sc-dropdown-item-remove {
        position: absolute;
        top: -2px;
        right: 0;
        font-size: 20px;
        line-height: 1;
    }
    
    .qodef-sc-dropdown-subtotal {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top:17px;

        .qodef-sc-dropdown-total {
            font-size: 12px;
        }
        
        > * {
            line-height: 16px;
            margin: 0;
        }
    }
    
    .qodef-sc-dropdown-button-holder {
        @include qodefRelativeHolderLayout();
    }

    .qodef-sc-dropdown-button-holder {
        @include qodefRelativeHolderLayout();

        .qodef-sc-dropdown-button-cart {
            @include qodefButtonDefaultStyle();
            @include qodefButtonSize();
            @include qodefButtonOutlineColor();
            width: 100%;
            text-align: center;
            font-size: 16px;
            margin-top: 17px;

            &:before {
                content: "\e079";
                font-family: ElegantIcons;
                speak: none;
                font-style: normal;
                font-weight: 400;
                font-variant: normal;
                text-transform: none;
                line-height: 1;
                -webkit-font-smoothing: antialiased;
                color: #bbb;
                margin-right: 8px;
            }

            &:hover {
                @include qodefButtonSolidHoverColor();
            }
        }

        .qodef-sc-dropdown-button-checkout {
            @include qodefButtonDefaultStyle();
            @include qodefButtonSize();
            @include qodefButtonSolidColor();
            width: 100%;
            text-align: center;
            font-size: 16px;
            margin-top: 10px;

            &:before {
                content: "\24";
                font-family: ElegantIcons;
                speak: none;
                font-style: normal;
                font-weight: 400;
                font-variant: normal;
                text-transform: none;
                line-height: 1;
                -webkit-font-smoothing: antialiased;
                color: #bbb;
                margin-right: 8px;
                transform: translateY(2px);
                display: inline-block;
            }

            &:hover {
                @include qodefButtonSolidHoverColor();
            }
        }
    }
}

.qodef-vertical-menu-area {

    .qodef-sc-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */