/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.qodef-content .woocommerce {
    @include qodefRelativeHolderLayout();
}

.qodef-pl-main-holder {
    margin: 0 0 22px;
    
    .qodef-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;
        
        .touch & {
            cursor: pointer;
        }

        .qodef-pl-inner {
            
            .qodef-pl-image {
                overflow: hidden;
        
                img {
                    display: block;
                    width: 100%;
                    transform: scale(1.05) translateX(-3px);
                    transition: .5s;
                    backface-visibility: hidden;
                }
            }
        }
        
        &:hover {
            
            .qodef-pl-inner {

                .qodef-pl-image{

                    img {
                        transform: scale(1.05) translateX(3px);
                    }
                }

                .qodef-pl-text {
                    opacity: 1;
                }
            }
        }

        .button,
        .added_to_cart {
            margin: 25px 0 0;
    
            .qodef-woo-pl-info-below-image & {
                margin: 0;
            }
        }
        
        .button.added {
            display: none;
        }
        
        .qodef-pl-inner {
            @include qodefRelativeHolderLayout();
            overflow: hidden;
            
            .qodef-pl-image {
                @include qodefRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                    transition: all .3s ease-out;
                }
            }
            
            .qodef-pl-text {
                @include qodefAbsoluteHolderLayout();
                opacity: 0;
                box-sizing: border-box;
                @include qodefImageZoomTransition();
                transition: all .3s ease-out;
            }
            
            .qodef-pl-text-outer {
                @include qodefTableLayout();
                text-align: center;
            }
            
            .qodef-pl-text-inner {
                @include qodefTableCellLayout(bottom);

                .qodef-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }

                .button, .added_to_cart{
                    font-size: 12px;
                    letter-spacing: .015em;
                }
            }
            
            > a {
                @include qodefAbsoluteHolderLayout();
            }
        }
        
        .qodef-pl-text-wrapper {
            @include qodefRelativeHolderLayout();
            margin: 20px 0 6px;
            z-index: 2;

            .qodef-title-price-wrapper{

                .qodef-product-list-title{
                    float: left;
                    display: inline-block;
                    width: 65%;
                    margin: 0;
                    line-height: 25px;
                    text-align: left;

                    a{
                        &:hover{
                            color: initial;
                        }
                    }
                }

                .price {
                    @include qodefWooCommercePriceStyle();
                    font-size: 17px;
                    float: right;
                    display: inline-block;
                    width: 35%;
                    margin: 0;
                    line-height: 25px;
                    text-align: right;
                    position: absolute;
                }

            }

            .qodef-pl-rating-holder {
                @include qodefRelativeHolderLayout();
                line-height: 1;
                margin: 4px 0;
            }

            .qodef-pl-category-holder{
                letter-spacing: 0;
                margin: 4px 0;

                > a {
                    color: $default-subheading-color;

                    &:hover{
                        color: $default-hover-color;
                    }
                }
            }
        }

        > a {
            @include qodefAbsoluteHolderLayout();
            z-index: 1;
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 2, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.qodef-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .qodef-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */