/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.qodef-pl-holder {
    @include qodefRelativeHolderLayout();

	.qodef-pli-onsale,
	.qodef-pli-out-of-stock,
	.qodef-pli-new-product {
		@include qodefWooCommerceMarksGeneralStyle();
	}

	.qodef-pli-onsale {
		@include qodefWooCommerceSaleStyle();
	}

	.qodef-pli-out-of-stock {
		@include qodefWooCommerceOutOfStockStyle();
	}

	.qodef-pli-new-product {
		@include qodefWooCommerceNewProductStyle();
	}

	.qodef-pli.outofstock{

		.qodef-pli-onsale{
			right: 80px;
		}

		.qodef-pli-new-product{
			right: 80px;
		}

		&.sale{
			.qodef-pli-new-product{
				right: 160px;
			}
		}
	}

	.qodef-pli.sale{

		.qodef-pli-new-product{
			right: 80px;
		}
	}
	
	&.qodef-info-on-image {
		
		.qodef-pli {
			
			.qodef-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

    .qodef-pli {
        padding: 0;
        z-index: 100;

		.qodef-pli-text-wrapper {
			@include qodefRelativeHolderLayout();
			margin: 20px 0 6px;
			z-index: 2;

			.qodef-title-price-wrapper {

				.qodef-pli-title {
					float: left;
					display: inline-block;
					width: 65%;
					margin: 0;
					line-height: 25px;
					text-align: left;

					a{
						&:hover{
							color: initial;
						}
					}
				}

				.qodef-pli-price {
					@include qodefWooCommercePriceStyle();
					font-size: 17px;
					float: right;
					display: inline-block;
					width: 35%;
					margin: 0;
					line-height: 25px;
					text-align: right;
					position: absolute;
				}

			}

			.qodef-pli-category {
				letter-spacing: 0;
				margin: 4px 0;

				> a {
					color: $default-subheading-color;

					&:hover {
						color: $default-hover-color;
					}
				}
			}
		}


	    .qodef-pli-rating {
		    @include qodefWooCommerceRatingsStyle();
	    }

        .qodef-pli-price {
	        @include qodefWooCommercePriceStyle();
        }
    }

    .qodef-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

			.qodef-pli-image{

				img {
					transform: scale(1.05) translateX(3px);
				}
			}

            .qodef-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .qodef-pli-image {
			@include qodefRelativeHolderLayout();
			overflow: hidden;
        
            img {
                display: block;
                width: 100%;
				transform: scale(1.05) translateX(-3px);
				transition: .5s;
				backface-visibility: hidden;
            }
        }
            
        .qodef-pli-text {
            @include qodefAbsoluteHolderLayout();
            opacity: 0;
            box-sizing: border-box;
            transition: opacity .2s ease-out;
        }

        .qodef-pli-text-outer {
	        @include qodefTableLayout();
	        text-align: center;
        }

        .qodef-pli-text-inner {
            @include qodefTableCellLayout();
			vertical-align: bottom;

            .qodef-pli-title,
            .qodef-pli-category a,
            .qodef-pli-excerpt,
            .qodef-pli-rating,
            .qodef-pli-price {
                color: #fff;
            }
	
	        .qodef-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .qodef-pli-add-to-cart {

		        &.qodef-default-skin {

			        .button,
			        .added_to_cart {
				        @include qodefButtonSolidColor();
						font-size: 12px;
						letter-spacing: .015em;
						padding: 6px 30px;

				        &:hover {
				            @include qodefButtonSolidHoverColor();
				        }
			        }
		        }

		        &.qodef-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.qodef-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include qodefButtonDefaultStyle();
	            @include qodefButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .qodef-pli-link {
		    @include qodefAbsoluteHolderLayout();
	    }
    }
	
	&.qodef-product-info-light {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: #fff;
				}
				
				.qodef-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.qodef-product-info-dark {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: $default-heading-color;
				}
				
				.qodef-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.qodef-masonry-layout {
		
		.qodef-pli-inner {
			width: 100%;
			height: 100%;
			
			.qodef-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */