/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {

    .qodef-onsale,
    .qodef-sold,
    .qodef-new-product {
        @include qodefWooCommerceMarksGeneralStyle();
    }

    .qodef-onsale {
        @include qodefWooCommerceSaleStyle();
    }

    .qodef-sold {
        @include qodefWooCommerceOutOfStockStyle();
    }

    .qodef-new-product {
        @include qodefWooCommerceNewProductStyle();
    }

    .product.outofstock{

        .qodef-onsale{
            right: 80px;
        }

        .qodef-new-product{
            right: 80px;
        }

        &.sale{
            .qodef-new-product{
                right: 160px;
            }
        }
    }

    .product.sale{

        .qodef-new-product{
            right: 80px;
        }
    }
}

.qodef-woo-single-page {

	.product.outofstock{

		.qodef-onsale{
			right: 80px;
		}

		.qodef-new-product{
			right: 80px;
		}

		&.sale{
			.qodef-new-product{
				right: 160px;
			}
		}
	}

	.product.sale{

		.qodef-new-product{
			right: 80px;
		}
	}

    &.qodef-woo-single-thumb-below-image {

		.qodef-onsale {
			right: 10px;
		}

		.qodef-sold {
			right: 10px;

		}

		.qodef-new-product {
			right: 10px;

		}

		.product.outofstock{

			.qodef-onsale{
				right: 90px;
			}

			.qodef-new-product{
				right: 90px;
			}

			&.sale{
				.qodef-new-product{
					right: 170px;
				}
			}
		}

		.product.sale{

			.qodef-new-product{
				right: 90px;
			}
		}
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */