/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.qodef-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: -12px 0 10px;
        float: left;
    }

    .woocommerce-result-count {
        line-height: #{$sselect-input-height}px;
        letter-spacing: 0;
    }

    .woocommerce-ordering {
        text-align: right;
        font-size: 0;

        .orderby {
            width: auto;
            min-width: 250px;
            margin: 0;
            text-align: initial;
        }

        .select2 {
            margin-bottom: 0;
            text-align: initial;

            .select2-selection--single {
                padding-left: 19px !important;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */