/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;

    .product-title {
        @include qodefDefaultHeadingStyle();
        @include qodefH6();
        margin: 39px 0 -3px 0;
    }

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include qodefRelativeHolderLayout();
                padding: 0;
                margin: 0 0 11px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include qodefRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include qodefRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
        margin: 0 0 41px;

        ul {
            
            li {

                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }

        > form{
            margin-bottom: 15px;

            > span{
                margin-bottom: 15px;
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .qodef-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {
        margin: 0 0 54px;

        .qodef-widget-title {
            margin: 0 0 33px;
        }
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #e5e5e5;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -3px;
                    height: 9px;
                    width: 9px;
                    border-radius: 50%;
                    background-color: $default-heading-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        transform: translateX(-50%);
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $default-heading-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 16px 0 0;
            
            .button {
                float: right;
                padding: 0;
                margin: 0;
                background: none;
                color: #222222;
                font-size: 16px;
                line-height: 23px;
                text-transform: lowercase;
                border: 0;

                &:hover{
                    color: $default-hover-color;
                    background: none;
                }
            }
            
            .price_label {
                float: left;
                font-size: 15px;
                letter-spacing: 0;
            }
        }
    }
    
    &.widget_product_categories {
        margin: 0 0 41px;

        > span{
            margin-bottom: -16px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        margin: 0 0 43px;
    
        .qodef-widget-title {
            margin: -4px 0 29px;
        }
        
        ul {
            
            li {
                min-height: 121px;
                font-size: 17px;

                > .woocommerce-Price-amount ~ .woocommerce-Price-amount {
                    margin: 0 !important;
                    float: none !important;
                    display: inline-block !important;
                }

                > ins{
                    margin: 0 0 0 10px !important;
                    float: none !important;
                    display: inline-block !important;
                }

                &:hover{

                    a {
                        img{
                            transform: scale(1.05) translateX(3px);
                        }
                    }

                    .star-rating {
                        opacity: 1;
                    }
                }
                
                a {
                    overflow: hidden;

                    img {
                        transform: scale(1.05) translateX(-3px) translateZ(0);
                        transform-origin: 50% 100%;
                        transition: .5s;
                        backface-visibility: hidden;
                    }

                    .qodef-around-image{
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 100px;
                        height: 121px;
                        overflow: hidden;
                    }
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 100px;
                        height: 121px;
                        object-fit: cover;
                        @include qodefImageZoomTransition();
                    }

                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 120px;
                        line-height: 25px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 120px;
                    line-height: 25px;
                }
                
                .star-rating {
                    margin: 5px !important;
                    position: absolute;
                    top: 45px;
                    left: 0;
                    opacity: 0;
                    font-size: 12px;
                    transition: opacity .2s ease-out;
                    background-color: $first-main-color;

                    span {
                        max-width: 100px;
                        font-size: 0;
                        height: 25px;

                        &:before{
                            font-size: 12px;
                        }
                    }
                }

                del{
                    color: #a1a1a1;
                    font-size: 80%;

                    > span{
                        color: #a1a1a1 !important;
                    }
                }
                
                del,
                ins {
                    display: inline-block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .amount {
                    color: $default-text-color;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }

                .amount {
                    display: block;
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */