/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {

    .qodef-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .qodef-quantity-input,
        .qodef-quantity-minus,
        .qodef-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
        }

        .qodef-quantity-input {
            position: relative;
            height: 50px;
            min-width: 75px;
            padding: 0;
            margin: 0;
            line-height: 50px;
            outline: 0;
            border: 1px solid $default-border-color;
            font-size: 15px;
            font-family: $default-heading-font;
            color: #000;
            z-index: 3;
        }

        .qodef-quantity-minus,
        .qodef-quantity-plus {
            position: absolute;
            top: 50%;
            right: 25px;
            width: 25px;
            height: 25px;
            line-height: 23px;
            cursor: pointer;
            transition: color .15s ease-in-out;
            z-index: 5;
            font-size: 16px;

            &:before{
                display: block;
                line-height: 25px;
            }

            &:hover {
                color: $default-hover-color;
            }
        }

        .qodef-quantity-plus {
            text-align: left;
            transform: translate(100%, -50%);
        }

        .qodef-quantity-minus {
            text-align: right;
            transform: translate(-100%, -50%);
        }
    }
}

.woocommerce-page .qodef-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */