/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.qodef-woo-single-page {
	
	&.qodef-woo-single-thumb-on-left-side {
		
		.qodef-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 115;
					$space_between_items: 16;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
							padding: 2px 0;
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include qodefRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;

		@include ipad-landscape {
			margin: 6px 0 41px;
		}
    }
        
    .qodef-single-product-content {
        @include qodefRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 51%;
		    margin: 0;
		    padding: 0 30px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include qodefRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .qodef-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 49%;
        padding: 0 0 0 36px;
        float: left;
        box-sizing: border-box;
	
	    .qodef-single-product-title {
		    display: block;
		    margin: 9px 0;
	    }
	
	    .price {
		    @include qodefWooCommercePriceStyle();
			font-size: 22px;
			margin: 0;
			letter-spacing: -0.02em;
	    }
	
	    .woocommerce-product-rating {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 17px 0 4px;

			> .woocommerce-review-link {
				letter-spacing: 0;
				color: #3d3d3d;
				transform: translateY(1px);
				display: inline-block;
				margin-left: 3px;

				&:hover{
					color: $default-hover-color;
				}
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include qodefRelativeHolderLayout();
		    margin: 30px 0 0;

		    p {
				margin: 0 61px 0 0;
				letter-spacing: 0;

				@media screen and (max-width: 1440px){
					margin: 0 24px 0 0;
				}

				@media screen and (max-width: 1024px){
					width: 90%;
				}
		    }
	    }
	
	    .product_meta {
		    margin: 44px 0 0;
		
		    > span {
			    display: block;
			    margin: 3px 0;
				font-size: 15px;
				color: #000;
				font-family: $default-heading-font;
				text-transform: uppercase;
				letter-spacing: .1em;

				* {
					font-family: $default-text-font;
					text-transform: initial;
					color: $default-text-color;
					letter-spacing: 0;

					&:first-child{
						margin-left: 4px;
					}
				}
			
			    a {
				    &:hover {
					    color: $default-hover-color;
				    }
			    }
		    }
	    }
	
	    .qodef-woo-social-share-holder {
		    @include qodefRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include qodefRelativeHolderLayout();
		    margin: 44px 0 0;

			> button{
				border: 1px solid $default-border-color !important;
				background: transparent !important;
				color: #222 !important;
				padding: 9px 35px !important;
				letter-spacing: 0 !important;

				&:before{
					content: "\e079";
					font-family: 'ElegantIcons';
					speak: none;
					font-style: normal;
					font-weight: normal;
					font-variant: normal;
					text-transform: none;
					line-height: 1;
					-webkit-font-smoothing: antialiased;
					color: #bbb;
					margin-right: 8px;
				}
			}
	    }
	
	    .qodef-quantity-buttons {
		    margin: 0 5px 0 0;
			border: 1px solid $default-border-color;
			padding-left: 117px;

			@media screen and (max-width: 480px){
				margin: 0 5px 10px 0;
			}

			.qodef-quantity-input{
				border: 0;
			}

			&:before{
				content: 'Quantity';
				display: block;
				position: absolute;
				left: 21px;
				top: 50%;
				transform: translateY(-50%);
				color: #3d3d3d;
				font-size: 16px;
				letter-spacing: 0;
			}
	    }
    }
	
	.woocommerce-tabs {
		@include qodefRelativeHolderLayout();
		margin: 88px 0 0;

		@media screen and (max-width: 1024px) {
			margin: 43px 0 0;
		}
		
		ul.tabs {
			@include qodefRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: left;
			clear: both;
			font-size: 0;
			display: flex;

			@media screen and (max-width: 480px){
				display: block;
			}
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;

				@media screen and (max-width: 480px){
					display: block;
				}
				
				a {
					@include qodefDefaultHeadingStyle();
					@include qodefH6();
					margin: 0;
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 37px;
					border-bottom: 2px solid $default-border-color;
					transition: border .2s ease;
				}

				&:hover{

					a{
						border-color: #000;
					}
				}

				&.active{

					a{
						border-color: #000;
					}
				}
			}
		}
		
		.entry-content {
			@include qodefRelativeHolderLayout();

			> h2 {
				display: none;
			}
			
			> p {
				margin: 50px 0 20px;
				letter-spacing: 0;

				@media screen and (min-width: 1025px){
					width: 80%;
				}
			}
		}
		
		table {
			margin: 47px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				width: 1%;
				padding: 0 10px 0 0;
				display: block;
				margin: 3px 0;
				font-size: 15px;
				color: #000;
				font-family: $default-heading-font;
				text-transform: uppercase;
				letter-spacing: .1em;
				font-weight: 400;

				&:after{
					content: ':';
				}
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include qodefRelativeHolderLayout();
			margin: 50px 0 20px;
			
			h2 {
				@include qodefDefaultHeadingStyle();
				@include qodefH4();
				margin: -2px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 24px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include qodefRelativeHolderLayout();
					
					img {
						position: relative;
						top: 10px;
						left: 10px;
						width: 82px;
						height: 82px;
						display: block;
						float: left;
						border-radius: 50%;
					}
				}
				
				.comment-text {
					padding: 10px 0 0 118px;
					min-height: 87px;

					.star-rating{
						margin: 0 0 10px;
					}
					
					p {
						margin: 0;
						display: flex;

						@media screen and (max-width: 480px){
							display: block;
						}

						.woocommerce-review__author{
							@include qodefDefaultHeadingStyle();
							@include qodefH5();
							margin: 0;
						}

						.woocommerce-review__dash, .woocommerce-review__published-date{
							line-height: 25px;
							margin: 0;
							font-size: 14px;
							color: #7d7d7d;

							@media screen and (max-width: 480px){
								display: block;
							}
						}

						.woocommerce-review__dash{
							margin: 0 10px;

							@media screen and (max-width: 480px){
								display: none;
							}
						}
					}

					.description{
						margin-top: 12px;
					}
				}
			}
			
			.comment-respond {
				margin-top: 43px;
				
				.comment-reply-title {
					@include qodefDefaultHeadingStyle();
					@include qodefH4();
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}

				.comment-form-cookies-consent{
					margin: 0 0 38px;
				}

				.comment-form-rating, .comment-form-comment{
					font-size: 17px;
					line-height: 26px;
					letter-spacing: 0;
					margin: 28px 0 0;
				}
				
				.stars {
					
					> span {
						position: relative;
						display: flex;
						margin: 10px 0;
					}
					
					&.selected {
					
						a {
							@include qodefWooCommerceSingleProductRatingsStyle(true);
						}
					}
					
					a {
						position: relative;
						display: block;
						color: transparent;
						margin: 0;
						font-size: 0;
						line-height: 1;
						@include qodefWooCommerceSingleProductRatingsStyle();

						&:before, &:after{
							font-size: 14px !important;
						}

						&:after{
							content: '\e031';
							position: absolute;
							left: 0;
							font-family: "ElegantIcons";
							line-height: 1;
							letter-spacing: 6px;
							color: $default-text-color;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 58px 0 -77px;

		> h2 {
			@include qodefDefaultHeadingStyle();
			@include qodefH4();
			display: block;
			margin: 0 0 50px;
			text-align: left;
		}
		
		.product {
			
			.qodef-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */