/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 20px;
    width: auto;
    height: 27px;
    min-width: 73px;
    margin: 0;
    padding: 0 16px;
    color: #000;
    font-size: 16px;
    letter-spacing: 0;
    line-height: 27px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    transform: translateZ(0);
}

@mixin qodefWooCommerceSaleStyle() {
    right: 0;
    background-color: #d6ebf3;
}

@mixin qodefWooCommerceOutOfStockStyle() {
    right: 0;
    background-color: #000;
    color: #fff;
}

@mixin qodefWooCommerceNewProductStyle() {
    right: 0;
    background-color: #fff;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 14px 0 0;
    line-height: 1;
    color: $default-heading-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        color: #a1a1a1;
        font-size: 80%;
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 6px;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin qodefWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin qodefWooCommerceSingleProductRatingsStyle($active-style: false) {
    $single-rating-default-icon: "\e031";
    $single-rating-active-icon: "\e033";
    
    @if ($active-style) {
    
        &.active {
    
            &:before {
                content: "\e033";
            }
            
            ~ a {
    
                &:before {
                    content: "\e031";
                }
            }
        }
        
        &:not(.active) {
            
            &:before {
                content: "\e033";
            }
        }
    } @else {
        
        &:before {
            content: "\e031";
            @extend %star-ratings;
        }
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */